import React from 'react'

const citations = [
    {
        name: 'React',
        href: ''
    },
    {
        name: 'Material-UI',
        href: 'https://mui.com/'
    },
    {
        name: 'Nasa Daily Api',
        href: 'https://api.nasa.gov/index.html'
    },
    {
        name: 'Cats Api',
        href: 'https://meowfacts.herokuapp.com'
    },
    {
        name: 'Cats Api 2',
        href: 'https://api.thecatapi.com'
    }
]

export default function Citations() {
    return (
        <div className="containter" style={{marginTop: '10vh',display: 'flex', flexDirection: 'row', justifyContent: 'space-between', alignItems: 'start'}}>
            <div className="tech">
            {
                citations.map((c,i) => (
                    <div key={i} className="li-citation">
                        {i}. <a className="link-slf hvr-lght--regular" href={c.href}>{c.name}</a>
                    </div>
                ))
            }
            </div>
        </div>
    )
}
