import * as React from 'react';
import PropTypes from 'prop-types';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Link from '@mui/material/Link';
import resume from '../documents/softwareperson.pdf'

function Header(props) {
  const { sections, title, HeaderHandler } = props;

  return (
    <div className="container">
      <Toolbar sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Typography
          component="h2"
          variant="h5"
          color="inherit"
          align="center"
          noWrap
          sx={{ flex: 1 }}
        >
          <span 
            className="font--explora link-slf hvr--default"
            onClick={() => HeaderHandler('')}>
            {title}
          </span>
        </Typography>
      </Toolbar>
      <Toolbar
        component="nav"
        variant="dense"
        sx={{ justifyContent: 'space-between', overflowX: 'auto' }}
      >
        {sections.map((section) => (
          <Link
            color="inherit"
            noWrap
            key={section.title}
            variant="body2"
            onClick={ () => HeaderHandler(section.title) }
            sx={{ p: 1, flexShrink: 0 }}
            className={`hvr-lght ${section.disable?'link--inactive':""}`}
          >
            {section.title}
          </Link>
        ))}

          <Typography>uddarendeli@gmail.com</Typography>
      </Toolbar>
    </div>
  );
}

Header.propTypes = {
  sections: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string.isRequired,
      url: PropTypes.string.isRequired,
    }),
  ).isRequired,
  title: PropTypes.string.isRequired,
};

export default Header;