import React, {useState, useEffect} from "react";
import Blog from './BlogPage/Blog'
import Citations from "./CitationPage/Citations";
import Frontend from "./FrontendPage/Frontend";
import Header from './BlogPage/Header';
import CssBaseline from '@mui/material/CssBaseline';


const sections = [
];
    


export default function App() {
    const [section, setSection] = useState('')
    useEffect(() => {
        setTimeout(() => {
            setProd(false)
        }, 1500);

    }, [])

    const HeaderHandler = (sec) => {
        setSection(sec)
    }

    const [prod, setProd] = useState(true)
    return (
        <>
        <CssBaseline />
        {
            prod ?
            <div onClick={() => setProd(false)} style={{position: 'fixed', margin: '30vh 30vw', width: '40vw', height: '40vh', backgroundColor: '#ccc', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', zIndex: '1', borderRadius: '30px'}}>
                <p>...In Production...</p>
                <p>Click to dismiss</p>
            </div> : ''
        }
            <Header title="Demut" sections={sections} HeaderHandler={HeaderHandler} />
        {
            section === '' ? <Blog /> :
            section === 'Technology' ? <Frontend /> :
            section === 'Open S' ? <Citations /> :
            <></>
        }
        </>
    )
}